.contact-form{
    width:100%;
}
form{
    display: flex;
    flex-direction: column;
    grid-gap:1.5rem;
}

.namme{
    display: flex;
    align-items: center;
    grid-gap:1.5rem
}
form input,form textarea{
    flex:1;
    width:95%;
    font-size: 0.9rem;
    color: #fff;
    background: #130f2a;
    border-radius: 0.6rem;
    border: 1.5px solid #6751b9;
    padding:1rem;
}

form button{
    font-size: 1.4rem;
    font-weight: bolder;
    color: #8064e8;
    border:1.5px solid #a892fe;
    background: linear-gradient(90deg #a892fe 0%,#8064e8 100%);
    padding: 0.7rem;
    transition: all 0.3 ease ;
    border-radius: 0.6rem;
    cursor: pointer;
}

form button:hover{
    color: #a892fe;
    background:transparent;
}
@media (max-width:768px) {
    .contact-form input textarea {
        margin-right: 1rem;
    }
}